import React, { useState, useEffect } from "react";
import { Container, Row, Col, ButtonGroup, Button } from "react-bootstrap";
import { Github, Linkedin } from "react-bootstrap-icons";
import TextTransition, { presets } from "react-text-transition";
import { MotionLeft, MotionRight } from "../../../../components";

import { color } from "../../../../utils";
const btn = {
  backgroundColor: color.main,
  borderColor: color.main,
  color: color.second,
  fontSize: "25px",
};

const TEXTS = ["React", "Javascript", "Typescript", "Firebase", "Node", "Jest"];

const Greeting = () => {
  const [index, setIndex] = useState(0);

  useEffect(() => {
    const intervalId = setInterval(
      () => setIndex((index) => index + 1),
      3000 // every 3 seconds
    );
    return () => clearTimeout(intervalId);
  }, []);
  return (
    <Container>
      <Row>
        <Col sm={6}>
          <MotionRight>
            <div
              className="BoxShadow"
              style={{
                background: color.main,
                padding: "30px",
                color: color.second,
              }}
            >
              <h1>Hæ! Ég heiti Jónas Helgi</h1>
              <p style={{ fontSize: "20px" }}>
                Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                Praesent viverra varius justo, eget consequat ex placerat vitae.
                Integer ullamcorper libero sed interdum pulvinar. Maecenas nec
                tellus dolor. Morbi imperdiet tellus vel tincidunt aliquet.
                Suspendisse laoreet gravida justo in ultricies. Proin quis leo
                at lorem convallis
              </p>
            </div>
            <div style={{ paddingTop: "60px" }}>
              <ButtonGroup
                style={{ boxShadow: "rgba(0, 0, 0, 0.24) 0px 3px 8px" }}
              >
                <Button size="lg" style={btn}>
                  <Github size={35} color={color.second} />
                </Button>
                <Button size="lg" style={btn}>
                  <Linkedin size={35} color={color.second} />
                </Button>
              </ButtonGroup>
            </div>
          </MotionRight>
        </Col>
        <Col sm={6}>
          <MotionLeft>
            <h2 style={{color: color.main, paddingBottom: "30px", paddingTop: "15px"}}>Ég hef unnið með</h2>
            <div style={{ display: "flex", justifyContent: "center" }}>
              <h1 style={{ color: color.main, fontSize: "80px" }}>
                <TextTransition
                  text={TEXTS[index % TEXTS.length]}
                  springConfig={presets.default}
                />
              </h1>
            </div>
          </MotionLeft>
        </Col>
      </Row>
    </Container>
  );
};

export default Greeting;
